import React from 'react'

import Navbar from "./Navbar";

import resume from "../Resume/resume.pdf";

const Resume = () => {
    return (
        <>
            <Navbar />
            <p align="center">
                <iframe title="Yongmin's Resume" src={resume} width="50%" height="1000px">
                    <a href={resume}>Download PDF</a>
                </iframe>
            </p>
        </>
    );
}

export default Resume;
