import React from 'react';
import { makeStyles } from "@material-ui/core/styles";
import { Box, Grid, Typography } from "@material-ui/core";

import ProgressBar from 'react-animated-progress-bar';

import Navbar from "./Navbar";

const useStyles = makeStyles(theme => ({
    mainContainer: {
        // backgroundColor: "white !important",
    },
    title: {
        color: "#f2a365",
        marginTop: "2rem",
        marginBottom: "8rem",
        fontFamily: 'Bangers',
    },
    subTitle: {
        color: "#e8e4e1",
        marginLeft: "8rem",
        marginRight: "8rem",
        marginBottom: "8rem",
        fontFamily: 'Bangers',
    },
    progress: {
        margin: "100rem",
    }
}));
const Skills = () => {
    const classes = useStyles();

    return (
        <Box component="div" className={classes.mainContainer} textAlign="center">
            <Navbar />
            <Typography className={classes.title} gutterBottom variant="h2">
                My Skills
            </Typography>
            <Grid container justify="center" alignItems="center">
                <Typography className={classes.subTitle} gutterBottom variant="h4">
                    C++
                    <ProgressBar className={classes.progress} width="230" trackWidth="13" percentage="90" />
                </Typography>
                <Typography className={classes.subTitle} gutterBottom variant="h4">
                    Javascript
                    <ProgressBar width="230" trackWidth="13" percentage="80" />
                </Typography>
                <Typography className={classes.subTitle} gutterBottom variant="h4">
                    React.Js
                    <ProgressBar width="230" trackWidth="13" percentage="75" />
                </Typography>
                <Typography className={classes.subTitle} gutterBottom variant="h4">
                    Node.Js
                    <ProgressBar width="230" trackWidth="13" percentage="80" />
                </Typography>
                <Typography className={classes.subTitle} gutterBottom variant="h4">
                    Express
                    <ProgressBar width="230" trackWidth="13" percentage="80" />
                </Typography>
                <Typography className={classes.subTitle} gutterBottom variant="h4">
                    MongoDB
                    <ProgressBar width="230" trackWidth="13" percentage="85" />
                </Typography>
                <Typography className={classes.subTitle} gutterBottom variant="h4">
                    Unity
                    <ProgressBar width="230" trackWidth="13" percentage="40" />
                </Typography>
                <Typography className={classes.subTitle} gutterBottom variant="h4">
                    HTML5, CSS
                    <ProgressBar width="230" trackWidth="13" percentage="90" />
                </Typography>
                <Typography className={classes.subTitle} gutterBottom variant="h4">
                    jQuery
                    <ProgressBar width="230" trackWidth="13" percentage="80" />
                </Typography>
                <Typography className={classes.subTitle} gutterBottom variant="h4">
                    OpenGL
                    <ProgressBar width="230" trackWidth="13" percentage="70" />
                </Typography>
            </Grid>

            
        </Box>
    )
}

export default Skills
