import React from 'react'
import { makeStyles } from "@material-ui/core/styles"
import { Typography, Avatar, Grid, Box } from "@material-ui/core"
import Typed from "react-typed";
import profile_img from "../img/profile.jpg";

const useStyles = makeStyles(theme => ({
    avatar: {
        width: theme.spacing(30),
        height: theme.spacing(30),
        margin: theme.spacing(1)
    },
    title: {
        color: "#f2a365"
    },
    subTitle: {
        color: "tan",
        marginBottom: "3rem",
    },
    typedContainer: {
        position: "absolute",
        top: "50%",
        left: "50%",
        transform: "translate(-50%, -50%)",
        width: "100vw",
        textAlign: "center",
        zIndex: 1
    }
}));

function Header() {
    const classes = useStyles();
    return (
        <Box className={classes.typedContainer}>
            <Grid container justify="center">
            <Avatar className={classes.avatar} src={profile_img} alt="Yongmin Cho"/>
            </Grid>
            <Typography className={classes.title} variant="h4">
                <Typed strings={["Hello, I'm Yongmin Cho"]} typeSpeed={40} />
            </Typography>
            <br/>
            <Typography className={classes.subTitle} variant="h5">
                <Typed 
                strings={["Front-end Development", "Back-end Development", "Game Development"]} 
                typeSpeed={40} 
                backSpeed={60}
                loop
                />
            </Typography>
        </Box>
    )
}

export default Header
